@use '../base/variables' as v;
@use '../base/mixins' as m;

.alerta{
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 400;
    margin: 1rem 0;
    font-size: 1.4rem;
    max-width: 80rem;

    &.error{
        background-color: lighten($color: v.$rojo, $amount: 45%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }

    &.exito{
        background-color: lighten($color: v.$verde, $amount: 45%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }

}